.item-image {
  height: auto;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transition: -webkit-transform 750ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: -webkit-transform 750ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 750ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 750ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 750ms cubic-bezier(0.23, 1, 0.32, 1); }
  .item-image::before {
    content: "";
    display: block;
    padding-top: 75%;
    overflow: hidden; }
  .item-image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    line-height: 0; }

.item-text {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  opacity: 0;
  text-align: center;
  z-index: 1;
  color: #ffffff;
  -webkit-transition: opacity 500ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: opacity 500ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: opacity 500ms cubic-bezier(0.23, 1, 0.32, 1), transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: opacity 500ms cubic-bezier(0.23, 1, 0.32, 1), transform 500ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition-delay: 300ms;
  transition-delay: 300ms;
  -webkit-transform: translateY(-20%);
  transform: translateY(-20%); }

.item-text-wrapper {
  width: 100%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  color: white;
  font-weight: bold; }

.item-text-title {
  font-weight: bold;
  padding: 0 15px;
  margin: 5px 0 0 0;
  color: white; }

.item-text-dek {
  text-transform: uppercase;
  opacity: 0.7;
  margin: 0; }

.item-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  line-height: 0;
  overflow: hidden;
  text-indent: -9999px; }

.certificate {
  height: 290px; }

.site-header {
  margin-bottom: 3rem; }

.site-branding {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: static;
  top: 0;
  left: 0;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, white), to(rgba(255, 255, 255, 0)));
  background: linear-gradient(to bottom, white 60%, rgba(255, 255, 255, 0) 100%);
  padding: var(--spacing-site) var(--spacing-site) 0.5em; }
  .site-branding p:nth-child(2) a {
    text-decoration: none; }
  .site-branding .fab {
    color: #444;
    padding: 0 .3em; }
  .site-branding .fab:hover {
    cursor: pointer;
    color: #607D89; }

.site-title {
  font-size: 2.3em;
  padding-bottom: 0.5rem;
  font-weight: bold; }
  .site-title a {
    text-decoration: none;
    color: inherit; }

.site-description {
  font-family: 'Open Sans';
  font-size: 100%; }

.large-header {
  position: relative;
  width: 100%;
  overflow: hidden;
  background-size: cover;
  background: rgba(255, 255, 255, 0.1) center center;
  z-index: 1; }

.navigation-bar {
  position: absolute;
  margin: 0;
  padding: 0;
  color: #f9f1e9;
  text-align: left;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  z-index: 2; }
  .navigation-bar li:hover {
    color: #f9f1e9; }

.social li {
  display: inline-block; }

.social .pop:hover {
  -webkit-animation: pop .3s 2;
  animation: pop .3s 2; }

.social .rotate-vertical:hover {
  -webkit-animation: pop 1.1s ease-out;
  animation: pop 1.1s ease-out; }

.social .full-circle:hover {
  -webkit-animation: full-circle 1.1s ease-out;
  animation: full-circle 1.1s ease-out; }

@media (max-width: 600px) {
  .talk-container {
    display: block; }
  .listing {
    display: block; }
    .listing li {
      width: 100%;
      margin: .6em auto; }
  #demo-canvas {
    display: none; }
  .navigation-bar {
    width: 75%; }
  .nav-toggle {
    font-size: 1.4rem; }
  .site-content {
    padding: 2em;
    font-size: 1.2em; }
  .site-header {
    margin-bottom: 0; }
  .desc {
    text-align: left; }
  .item-grid {
    margin: 0 auto; }
  .item {
    float: none;
    width: 100%; } }

@media only screen and (max-width: 350px) {
  .listing .post-tag,
  .listing a .author {
    display: none; } }
